.autoBreaths-control, #autoBreaths-switch {
    padding: 20px 0;
}

.autoBreaths-control h2 {
    width: 100%;
    margin-bottom: 20px;
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #f2f2f2;
}

.autoBreaths-control .autoBreaths-control-slider {
    margin: auto;
}

#autoBreaths-switch h3 {
    margin: 0;
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
}

#autoBreaths-switch {
    background-color: #cccccc;
}

.autoBreaths-labels {
    margin-left: 10px;
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    text-transform: uppercase;
    color: #f2f2f2;
}
