#scriptInput-masterScript {
    border-radius: 4px;
    padding: 16.5px 14px;


}

#scriptInput-masterScript-text {
    height: 207px;
    background-color: #e6e6e5;
    padding-left: 5px;
    overflow: auto;
}

#sectionFooter-inputScript {
    margin-bottom: 15px;
    font-family: 'futura-pt', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    text-transform: uppercase;
    color: #ff00ff;
}

#wordCount-inputScript {
    text-align: left;
    padding-left: 20px;
}

#scriptLength-inputScript {
    text-align: right;
    padding-right: 20px;
}

.editWord {
    color: #0000ff;
}

.selectWord {
    background-color: #a689bb;
}