.selectView-labels {
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    text-transform: uppercase;
    color: #f2f2f2;
}

#tableHead-selection {
    background-color: #4d4e4e
}

    #tableHead-selection .tableCell-selection-cap {
        padding: 0;
    }

#table-selection th {
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    text-align:center;
    text-transform: uppercase;
    color: #f2f2f2;
}

.wordDisplayNavBtn-selection {
    height: 100%;
    width: 100%;
}

.tableCell-selection-cap {
    background-color: #4d4e4e;
    border: none !important;
}

#editingSwitches-selection .button {
    width: 120px;
}

.switch-button {
    background-image: linear-gradient(#a59fb3, #353140);
    color: #f2f2f2;
}

.switchButton-editing-selection_active {
    background-image: none !important;
    background-color: #93278f !important;
}