#downloadAudioButton-container {
    /* --- vertical align top ---*/
    -webkit-align-self: unset;
    -ms-flex-item-align: unset;
    align-self: unset;
    /* ---------------------------- */
}

#downloadAudioButton {
    width: 229px;
    height: 100px;
    margin: auto;
    display: flex;
    font-family: 'futura-pt', sans-serif;
    font-style: normal;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 22px;
    word-spacing: 229px;
    line-height: 25px;
    color: #ffffff;
    background-image: url("downloadAudio-button.png");
}

    #downloadAudioButton:hover {
        color: #0000ff;
    }

    #downloadAudioButton:active {
        color: #662d91;
    }

    #downloadAudioButton:disabled {
        opacity: 0.5;
    }
