#credits-container {
    background-color: #333132;
}

#credits-content {
    height: 262px;
    /* --- vertical align center ---*/
    box-sizing: border-box;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    /* ---------------------------- */
}

#credits-image {
    margin:auto;
}
