html {
    min-height: 100vh;
    background-image: url("SpeakEasy-backgroundWave.png");
    background-repeat: repeat-x;
    background-position-x: 605.5px;
    background-position-y: 190px;
    background-color: #0f0f19;
}

#body-grid {
    max-width: 1224px;
    margin: 60px auto;
    padding: 0 50px;
}

.body-grid-block {
    margin-top: 30px !important;
    margin-bottom: 30px !important;
    /* --- vertical align center ---*/
    -webkit-align-self: center;
    -ms-flex-item-align: center;
    align-self: center;
    /* ---------------------------- */
}

.module {
    background-color: #0f0f19;
    border: solid;
    border-width: 2.5px;
    border-color: #f2f2f2;
}

.popUpWindow {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 1;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,0.5);
    display: none;
}

.popUpContainer {
    border-style: solid;
    border-color: #ffd26e;
}

.moduleHeading {
    display: flex;
    border-bottom: solid;
    border-width: 2.5px;
    border-color: #f2f2f2;
}

.moduleHeading-magenta {
    background-color: #93278f;
}

.moduleHeading-lightPurple {
    background-color: #662d91;
}

.moduleHeading-darkPurple {
    background-color: #2d0044;
}

.moduleHeading-blue {
    background-color: #0000ff;
}

.moduleHeading h1 {
    font-family: 'futura-pt', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    text-transform: uppercase;
    text-indent: 30px;
    color: #ffffff;
}

.moduleHeading-buttons {
    display:flex;
    padding-right: 10px;
}

    .moduleHeading-buttons button {
        margin: 0 7.5px;
        padding: 3px 25px;
        background-color: #ffffff;
        border-style: none;
        border-radius: 3px;
        font-family: 'futura-pt', sans-serif;
        font-style: normal;
        font-weight: 600;
        text-transform: uppercase;
        font-size: 12px;
        color: #000000;
        mix-blend-mode: screen;
        cursor: pointer;
    }

        .moduleHeading-buttons button:hover {
            background-image: linear-gradient(#e80600, #bf00ff);
            color: #ffffff;
        }

        .moduleHeading-buttons button:active {
            color: #000000;
        }

        .moduleHeading-buttons button:disabled {
            background-image: none;
            opacity: 0.5;
            color: currentColor;
            cursor: default;
        }

.textButton {
    margin: 0;
    padding: 0 20px;
    border-style: none;
    background-color: #00000000;
    font-family: 'futura-pt', sans-serif;
    font-style: normal;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 19px;
    color: #191919;
    cursor: pointer;
}

    .textButton:hover {
        color: #0000ff;
    }

    .textButton:active {
        color: #93278f;
    }

    .textButton:disabled {
        color: #909090;
        cursor: default;
    }

.button-dividerRight {
    border-right-style: solid !important;
    border-right-width: 2px !important;
    border-right-color: #0f0f19 !important;
}