.masterControls-control {
    padding: 10px 20px;
}

.masterControls-controlDivider {
    border-top: solid;
    border-width: 2.5px;
    border-color: #f2f2f2;
}

.masterControls-control h2 {
    margin: 10px 0;
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    text-transform: uppercase;
    color: #f2f2f2;
}

#masterControls-switch h3 {
    margin: 0 0 5px 0;
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;
    color: #f2f2f2;
}

.masterControls-control .inputField {
    background-color: #f2f2f2;
    border-radius: 3px;
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    text-transform: uppercase;
}

.masterControls-control .inputField input {
    padding: 0 5px;
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    text-align:center;
    text-transform: uppercase;
}

.masterControls-control .checkBox {
    display:flex;
    margin: 0 auto;
}

.masterControls-labels {
    font-family: futura-pt, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    text-align: left;
    text-transform: uppercase;
    color: #f2f2f2;
}

    .masterControls-labels .leftRight-label {
        margin: auto 0;
    }

    .masterControls-labels .central-label {
        margin: auto;
    }