#footer-container {
    background-color: #e6e6e5;
}

#footer-content {
    max-width: 1224px;
    height: 180px;
    margin: auto;
    padding: 0 50px;
    /* --- vertical align center ---*/
    box-sizing: border-box;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    /* ---------------------------- */
}

#footer-menu {
    width: fit-content;
    margin: auto;
}
