#saveScript-container {
    width: 100%;
    height: 100%;
    max-width: 300px;
    max-height: 200px;
    background-color: white;
}

#closeBtnContainer-saveScript {
    width: fit-content;
    margin-left: auto;
}

#closeButton-saveScript {
    color: #ffffff !important;
    background-color: transparent;
}

    #closeButton-saveScript:hover {
        color: #ffd26e !important;
    }

    #closeButton-saveScript:disabled {
        color: #909090 !important;
    }

#saveScript {
    margin: 15.5% 0;
}

#textFieldContainer-saveScript {
    margin: auto;
}

#textFieldLabel-saveScript {
    padding: 8px 0;
    margin-right: 5px;
    display: inline;
    font-size: 13px;
    font-weight: 600;
    background-image: linear-gradient(#ffd26e, #feda88, #ffd26e);
    background-clip: text;
    color: transparent;
}

#scriptNameTextField-saveScript {
    font-size: 13px;
    background-color: #ffffff;
    padding: 0 0 0 5px;
}

#saveButton-saveScript {
    width:50px;
    margin: 15px auto;
}